/*
 * strategy
 */

.about-strategy {
  padding-bottom: unit(2);
  padding-top: unit(1);
}

.about-strategy__sidebar {
  @include null-margins;
  @include un-smooth-text;
  background-color: $color--white;
  color: $color--tuatara;
  padding: unit(2) unit(1);

  @include respond-to('medium-large') {
    height: 100%;
    padding: unit(2);
    text-align: center;
  }
}

.about-strategy__strategies {
  @include reset-list;
}

.about-strategy__strategies__item {
  clear: both;
  line-height: 1.4;
  min-height: unit(2.7);
  padding-left: unit(3.5);
  position: relative;

  + .about-strategy__strategies__item {
    @include respond-to('medium-large') {
      margin-top: unit(2);
    }
  }

  &::before {
    border: 2px solid $color--jelly-bean;
    border-radius: 50%;
    color: $color--jelly-bean;
    display: block;
    font: 2.4rem $font-family--kapra-condensed;
    left: 0;
    line-height: calc(#{unit(2.5)} - 4px);
    padding: 0;
    position: absolute;
    text-align: center;
    top: unit(0.2);
    width: unit(2.5);

    @include respond-to('medium-large') {
      font-size: 3.6rem;
      left: 50%;
      line-height: calc(#{unit(3)} - 4px);
      margin-left: unit(-1.5);
      width: unit(3);
    }
  }

  @include respond-to('medium-large') {
    min-height: 0;
    padding-left: 0;
    padding-top: unit(4);
  }
}

.about-strategy__approaches {
  @include reset-list;

  [class*='heading-'] {
    margin-top: 0;
  }
}

.about-strategy__approaches__item {
  @include respond-to('small') {
    display: flex;
  }

  + .about-strategy__approaches__item {
    margin-top: unit(2);

    @include respond-to('small') {
      margin-top: unit(1);
    }
  }
}

.about-strategy__approaches__image {
  height: auto;
  width: 100%;

  @include respond-to('small') {
    align-self: flex-start;
    width: 36%;
  }
}

.about-strategy__approaches__content {
  @include null-margins;
  font-size: 14px;

  @include respond-to('small') {
    float: right;
    padding-left: unit(1);
    width: 64%;
  }
}

/*
 * partners
 */

.about-partners__list {
  @include reset-list;

  @include respond-to('small') {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.about-partners__list__item {
  @include null-margins;
  margin: 0;

  &:nth-child(n + 2) {
    margin-top: unit(2);
  }

  @include respond-to('small') {
    width: calc(50% - #{unit(1)});

    &:nth-child(2) {
      margin-top: 0;
    }

    &:nth-child(n + 3) {
      margin-top: unit(2);
    }
  }

  @include respond-to('medium') {
    width: calc(50% - #{unit(2)});
  }

  @include respond-to('large') {
    width: calc(33.3334% - #{unit(2)});

    &:nth-child(3) {
      margin-top: 0;
    }
  }

  @include respond-to('x-large') {
    width: calc(33.3334% - #{unit(3)});
  }

  a {
    @include hover-underline;
  }
}

.about-partners__list__heading {
  align-items: center;
  color: $color--jelly-bean;
  display: flex;
  font: 1.8rem/1.3 $font-family--kapra-expanded;
  margin-bottom: unit(-0.4);
  text-transform: uppercase;

  img {
    height: unit(2.25);
    margin-right: unit(0.8);
    width: auto;
  }
}
